import React from 'react'

function NotFound() {
    return (
        <div>
            <h1 className="display-4"><span className="text-danger">404 {' '}</span>
            Page Not Found</h1>
            <p className="lead">Sorry, that page does not exists</p>
        </div>
    )
}

export default NotFound



